import { BreadcrumbsSize, ICrumb } from './types';

/** Функция подсчета длины строки исходя из среднего значения ширины одного символа
 * За основу подсчета были взято буквы "Щia"
 * Щ - самая широкая
 * i - самая узкая
 * а - средняя
 * */
export const getStrWidth = (arr: ICrumb[], size: string): number => {
  if (arr.length < 1) return 0;

  const sizeOfSeparator = 23.5;
  const sizeOfCollapsed = 20;

  const str = arr.map(crumb => crumb.title).join('');
  let oneLetter;

  switch (size) {
    case 'default':
      oneLetter = 28 / 3;
      break;
    case 'small':
      oneLetter = 25 / 3;
      break;
    case 'extra_small':
      oneLetter = 20 / 3;
      break;
    default:
      oneLetter = 28 / 3;
  }

  return str.length * oneLetter + (arr.length - 1) * sizeOfSeparator + sizeOfCollapsed;
};

export const getBreadcrumbsArray = (breadcrumbs: ICrumb[], elementsFit: boolean): ICrumb[] => {
  const collapsedBreadcrumb = { title: 'collapsed', isCollapsed: true } as ICrumb;
  if (elementsFit) {
    return breadcrumbs;
  }
  if (breadcrumbs.length > 1) {
    return [breadcrumbs[0], collapsedBreadcrumb].concat(breadcrumbs.slice(1));
  }

  return [collapsedBreadcrumb, breadcrumbs[0]];
};

export const getCurrentSize = (size: BreadcrumbsSize, isPath: boolean): 'default' | 'small' => {
  switch (size) {
    case 'small':
      return isPath ? 'default' : 'small';
    case 'extra_small':
      return 'small';
    default:
      return 'default';
  }
};
